.contact{
    padding:100px 50px;
}



.subscribe{
    text-align: center;
    margin-bottom: 20px;
}
.subscribe input{
width:50%;
padding:10px;
border: none;
background: rgba(0,0,0,0.05);
outline: none;
border-radius: 5px;
}
.subscribe button{
    padding:10px;
    border:none;
    outline: none;
    color:white;
    background: #7f8fa6;
    border-radius: 10px;
    margin-left: 20px;
}
.contact-row{
    display:flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
}
.contact-row  p{
    color:grey;
    margin: 20px 0;
    font-size: 20px;
}
.social ul {
    display:flex;
    list-style: none;
    
}
.social h2{
    text-align: center;
}

.social ul li{
    color:orchid;
    font-size: 25px;
    margin: 15px;
padding-right: 10px;
}

@media (max-width:468px){
    .social {
        margin-top:30px;
    }
    .corner{
       margin-top: 30px;
       text-align: center;
    }
}








