.product h1{
text-align: center;
font-size: 40px;
font-weight: bolder;
margin-top: 50px;

}
.selling-row {
    display:flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;

}
.selling-box{
    width:350px;
    margin: 30px;
    text-align: center;
    
}
.selling-box img{
    width:250px;
    height:200px;
    
    
}
.selling-box p{
    color:red;
    margin-top: 5px;
}
.selling-box h3{
    margin-top: 10px;
}
.selling-box img:hover{
    transform: scale(1.1);
}








