.About-row{
    display:flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding:50px 50px;
}
.about-left-col{
    flex-basis: 50%;
}
.about-left-col img{
    width:40%;
    margin-left: 20%;
}
.about-right-col{
    flex-basis: 50%;
    max-width: 500px;
    margin-bottom: 60px;

}



.circle2{
    width:400px;
    height:400px;
    position: absolute;
    border-radius: 50%;
    background: rgba(0,0,0,0.05);
    left:7%;
    z-index: -1;
}
.about-right-col  > h1{
    font-size: 40px;
}

.about-right-col  > p{
    color:grey;
    margin:20px 0px;
    font-family: italic;
    font-size: 19px;
}
.c-btn{
    padding:20px;
    color:white;
    background: #7f8fa6;
    border:none;
    outline:none;
    border-radius: 10px;

}
@media (max-width:468px){
    .About-row{
        display: flex;
        flex-direction: column;
    }
    .about-right-col{
        flex-basis: 100%;
    }
    .about-right-col   p{
        font-size: 16px;
    }

    .about-left-col{
        flex-basis: 100%;

    }
    .about-left-col img{
        width:50%;
        margin-bottom: 50px;
    }
    .circle2{
        margin-right: 10%;

    }
}














