*{
    padding:0;
    margin:0;
    box-sizing: border-box;
}
.header{
    width:100%;
    height:100%;
}
.header > .nav{
    display:flex;
    justify-content: space-around;
    align-items: center;
    width:100%;
    height: 70px;

}
.logo
{
    font-size: 30px;
    font-weight: 600;
    color:#7f8fa6;
}
.menu > a{
    text-decoration: none;
    font-size: 21px;
    color:white;
    font-weight: bold;
    padding:10px 25px;
}

.menu > a:hover{
    background-color: orchid;
    padding:10px;
    border-radius: 10px;
    transition: 2s;

}
.icon > .fa-couch{
    color: red;
    font-size: 25px;
}
.svg{
    position: absolute;
    top:0;
    left:0;
    width:100%;
    z-index: -1;

}
.icon .fa-bars{
    color:white;
    font-size: 30px;

}

.icon .fa-times{
    color:white;
    font-size: 30px;

}
.icon .fa-bars{
   display:none;

}

.icon .fa-chair{
    color:white;
    font-size: 30px;

}

@media (max-width:468px){
    
    .icon .fa-bars{
        display:block;     
    }

    .icon .fa-chair{
       display:none;
    
    }
    
    .menu {
       display:flex;
        flex-direction: column;
        width:100%;
        position: absolute;
         right:0;
         top:80px;
        background-color: rgba(0,0,0.7);
        padding:20px;
        transition: all 0.5s ease-in-out;

 
    }

.menu > a{
    width:100%;
    text-align: center;
    margin-bottom: 30px;
}


    .menu.close{
        right:-500px;
        transition: all 0.5s ease-in-out;
    }

    .logo
{
    font-size: 22px;
    font-weight: 600;
    color:#7f8fa6;
}
.icon > .fa-couch{
    color: red;
    font-size: 20px;
}



}





