.feature-row{
    display:flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding:50px 50px;
}
.feature-col-left{
    flex-basis: 50%;
    max-width: 450px;
}
.feature-col-right{
    flex-basis: 50%;

}
.feature-col-right  img{

width:70%;
margin-left: 30%;

}
.circle3{
    width:400px;
    height:400px;
    position: absolute;
    border-radius: 50%;
    background: rgba(0,0,0,0.05);
    right:10%;
    z-index: -1;
}
.feature-col-left > h1{
    color:orchid;
    font-size: 40px;

}
.feature-col-left > p{
    color:grey;
    margin:20px 0px;
    font-family: italic;
    font-size: 19px;
}
.c-btn{
    padding:20px;
    color:white;
    background: #7f8fa6;
    border:none;
    outline:none;
    border-radius: 10px;

}
@media (max-width:468px){

    .feature-row{
        display:flex;
        flex-direction: column;
    }
    .feature-col-left p{
font-size: 16px;
    }
.feature-col-right img{
    margin-top: 40px;
    margin-left: auto;
    width:100%;
}
}















