.circle{
    width:400px;
    height:400px;
    background: rgba(0,0,0,0.05);
    border-radius: 50%;
    position: absolute;
    top:25%;
    right:10%;
    z-index: -1;
    
}
.container{
    display:flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    width:100%;
    height:100vh;


}
.home-right{
    flex-basis: 40%;
    text-align: right;
    
}
.home-left{
    flex-basis: 60%;
    max-width: 450px;
    margin-top: 60px;


}
.home-right > img{
    width:70%;
    margin-top: 30px;
}
.home-left > h1{
    font-size: 50px;
}
.home-left span{
    color: silver;
}
.home-left  p{
    margin-top: 20px;
}
.home-left > .btn{
    margin-top: 30px;
}
.btn input{
    padding:10px;
    width:75%;
    border-radius: 10px;
    border:1px solid grey;
    outline: none;
}
.btn > button{
    padding:10px;
    color:white;
    background: #7f8fa6;
    border:none;
    outline:none;
    border-radius: 10px;
    margin-left: 10px;
}
.counter{
    display:flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 30px;

}

#welcome{
    position: absolute;
    bottom:0;
    left:0;
    width:180px;
}





@media (max-width:468px){
    .container{
        display:flex;
        flex-direction: column;
        width:100%;
    }
    .home-left{
        width:100%;
        margin:60px 20px;
    }

    .home-right{
        display:none;
    }
    #welcome{
        width:50px;
        margin-left: 60px;
        margin-top: 30px;
        
    }
    button{
        margin-top: 20px;
        margin-left: 60px;
    }
}








